<template>
  <div class="bg-white px-[20px] py-[16px]">
    <PageTitleWithGoBackButton> Markazga Xodim qo'shish </PageTitleWithGoBackButton>
    <section class="p-2 grid grid-cols-3 gap-3 w-full lg:w-[85%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="position"> Lavozimi </label>
        <SelectBox id="position" v-model="position" :options="positionsList" show-by="name" track-by="code" placeholder="Lavozimni tanlang" />
      </div>
    </section>
  </div>
</template>

<script setup>
import {ref, watch} from 'vue';
import {useRouter} from 'vue-router';
import SelectBox from '../components/Common/SelectBox.vue';
import PageTitleWithGoBackButton from '../components/LayoutItems/PageTitleWithGoBackButton';
import usePositions from '../composables/selectbox-list/usePostions';

const router = useRouter();

const DIRECTOR = 2;
const NAVBATCHI = 3;

const {positionsList} = usePositions();

const position = ref('');

watch(
  () => position.value,
  () => {
    console.log(position.value);
    const basicWorker = [DIRECTOR, NAVBATCHI];
    if (basicWorker.includes(position.value)) {
      router.push({
        name: 'add-worker-to-center',
        query: {
          position: position.value,
        },
      });
    } else {
      router.push({
        name: 'add-worker-to-center-monitoring',
      });
    }
  }
);
</script>

<style scoped>
span {
  @apply text-red-400 h-5;
}
</style>
